const { getPropertyValues } = require('devicetree/lib/helpers/properties')

const MISSING_ROOT_NODE = 'MISSING_ROOT_NODE'
const USING_ZMK_NODEFREE_MACROS = 'USING_ZMK_NODEFREE_MACROS'
const USING_ZMK_MACRO_MACRO = 'USING_ZMK_MACRO_MACRO'
const USING_MULTIPLE_ROOT_NODES = 'USING_MULTIPLE_ROOT_NODES'
const USING_MIRYOKU_DTSI = 'USING_MIRYOKU_DTSI'
const USING_NON_ZMK_MACRO = 'USING_NON_ZMK_MACRO'

function keymapUsesNodefreeConfigHelpers (document) {
  const macroNames = [
    'ZMK_BEHAVIOR',
    'ZMK_COMBO',
    'ZMK_CONDITIONAL_LAYER',
    'ZMK_LAYER',
    'UC_MACRO'
  ]

  for (const node of document.tree.rootNode.namedChildren) {
    if (node.type === 'ERROR') {
      const { text } = node
      if (macroNames.some(name => text.startsWith(name))) {
        return USING_ZMK_NODEFREE_MACROS
      }
    }
  }
}

function keymapUsesMiryoku (document) {
  for (const node of document.tree.rootNode.namedChildren) {
    if (node.type === 'preproc_include') {
      if (node.childForFieldName('path').text.includes('miryoku.dtsi')) {
        return USING_MIRYOKU_DTSI
      }
    }
  }
}

function keymapBindingsUsePreprocessorMacros (document) {
  const zmkPreprocFunctions = [
    'LA', 'LC', 'LG', 'LS',
    'RA', 'RC', 'RG', 'RS',
    'RGB_COLOR_HSB'
  ]

  for (const layerNode of document.layerCollection.nodes) {
    const [bindingsNode] = getPropertyValues(layerNode, 'bindings')
    const nonZmkPreprocFunction = bindingsNode.namedChildren.find(node => (
      node.type === 'call_expression' &&
      !zmkPreprocFunctions.includes(node.childForFieldName('function').text)
    ))

    if (nonZmkPreprocFunction) {
      return USING_NON_ZMK_MACRO
    }
  }
}

function keymapHasNoRootNodes (document) {
  for (let node of document.tree.rootNode.namedChildren) {
    if (node.type === 'labeled_item') {
      node = node.childForFieldName('item')
    }
    if (node.type === 'node' && node.childForFieldName('name').text === '/') {
      return
    }
  }

  return MISSING_ROOT_NODE
}

/**
 * Check if a devicetree document uses multiple root nodes.
 *
 * People sometimes organize different parts of a keymap (like behaviors and
 * combos) into separate instances of a root node. The devicetree compiler will
 * merge these root nodes into one, but preserving this for editing is annoying
 * and inefficient so I don't want to support it anymore.
 *
 * @param {DevicetreeDocument} document
 * @returns {String|undefined}
 */
function keymapHasMultipleRootNodes (document) {
  let firstRootNode = null

  for (let node of document.tree.rootNode.namedChildren) {
    if (node.type === 'labeled_item') {
      node = node.childForFieldName('item')
    }
    if (node.type === 'node' && node.childForFieldName('name').text === '/') {
      if (firstRootNode !== null) {
        return USING_MULTIPLE_ROOT_NODES
      }

      firstRootNode = node
    }
  }
}

/**
 * This is just a last-ditch check to see if keymap parsing failed because of a
 * custom define that replaced the initial binding cell's behavior. This would
 * otherwise be caught by `keymapBindingsUsePreprocessorMacros` except that the
 * initial grouping fails.
 * @param {DevicetreeDocument} document
 */
function keymapHasLayerBindingsWithNonBehaviorStarter (document) {
  return document.layerCollection.nodes.some(node => {
    const bindingCells = [].concat(...getPropertyValues(node, 'bindings'))
      .flatMap(integerCells => integerCells.namedChildren)
    const firstBindType = bindingCells[0]?.type

    return firstBindType && firstBindType !== 'reference'
  })
}

module.exports = {
  keymapHasNoRootNodes,
  keymapUsesNodefreeConfigHelpers,
  keymapUsesMiryoku,
  keymapBindingsUsePreprocessorMacros,
  keymapHasMultipleRootNodes,
  keymapHasLayerBindingsWithNonBehaviorStarter,
  warningFlags: {
    MISSING_ROOT_NODE,
    USING_ZMK_NODEFREE_MACROS,
    USING_ZMK_MACRO_MACRO,
    USING_MULTIPLE_ROOT_NODES,
    USING_MIRYOKU_DTSI,
    USING_NON_ZMK_MACRO
  }
}
