const filter = require('lodash/filter')
const last = require('lodash/last')
const reverse = require('lodash/reverse')

const { DevicetreeDocument } = require('devicetree')
const {
  nodesAreConsecutive,
  findChildByIdentifier,
  findChildrenByIdentifier,
  getLabeledItem
} = require('devicetree/lib/helpers')
const { standardBehaviorIncludeFiles } = require('zmk-data/behaviors')

const MacroCollection = require('./collections/macros')
const ComboCollection = require('./collections/combos')
const LayerCollection = require('./collections/layers')
const BehaviorCollection = require('./collections/behaviors')

class KeymapDocument extends DevicetreeDocument {
  constructor (...args) {
    super(...args)
    this.macroCollection = new MacroCollection(this)
    this.comboCollection = new ComboCollection(this)
    this.layerCollection = new LayerCollection(this)
    this.behaviorCollection = new BehaviorCollection(this)
  }

  cleanup () {
    super.cleanup()
    delete this.layerCollection
    delete this.comboCollection
    delete this.macroCollection
    delete this.behaviorCollection
  }

  getChosenProperties () {
    const rootNodes = findChildrenByIdentifier(this.tree.rootNode, '/')
    const chosen = findChildByIdentifier(rootNodes, 'chosen')

    if (!chosen) {
      return {}
    }

    return chosen.namedChildren.reduce((acc, child) => {
      if (child.type === 'labeled_item') {
        child = getLabeledItem(child)
      }
      if (child.type === 'property') {
        const name = child.childForFieldName('name').text
        const value = child.childForFieldName('value').text
        acc[name] = value
      }

      return acc
    }, {})
  }

  mergeUpdated (updated, featureFlags = {}) {
    this.layerCollection.updateCollection(updated.layers)
    this.comboCollection.updateCollection(updated.combos)
    this.macroCollection.updateCollection(updated.macros)

    if (featureFlags.enableBehaviorEditing) {
      this.behaviorCollection.updateCollection(updated.behaviors)
    }
  }

  replaceBehaviourIncludes (updated) {
    const replacementText = updated.join('\n') + '\n'
    const behaviorIncludes = this.tree.rootNode.namedChildren.filter(node => (
      node.type === 'preproc_include' &&
      // TODO: update behavior overlays to just specify the include path and not
      // the include pre-processor statement. `startsWith` is necessary here
      // because preproc_include node parsing includes trailing newline(s).
      standardBehaviorIncludeFiles.some(line => node.text.startsWith(line))
    ))

    if (behaviorIncludes.length) {
      if (nodesAreConsecutive(behaviorIncludes)) {
        this.replaceRange(
          behaviorIncludes[0].startIndex,
          behaviorIncludes.at(-1).endIndex,
          ''
        )
      } else {
        for (const node of reverse(behaviorIncludes)) {
          this.removeNode(node)
        }
      }
    }

    const lastInclude = last(filter(
      this.tree.rootNode.children,
      { type: 'preproc_include' }
    ))
    const firstDefine = this.tree.rootNode.children.find(node => node.type === 'preproc_def')
    const firstDtNode = this.tree.rootNode.children.find(node => node.type === 'node')

    if (lastInclude) {
      this.insertTextAfterNode(lastInclude, replacementText)
    } else if (firstDefine || firstDtNode) {
      this.insertTextBeforeNode(
        firstDefine || firstDtNode,
        replacementText
      )
    }
  }
}

module.exports = KeymapDocument
