const filter = require('lodash/filter')
const get = require('lodash/get')
const { parsePropertyFromSchema } = require('devicetree/lib/helpers/properties')

/** @typedef {String} IntegerCell */
/** @typedef {String} Bind */
/** @typedef {{ value: String, params: Array<BindValue> }} BindValue */

/**
 * @param {SyntaxNode} node
 * @param {String} [propertyName='bindings']
 * @returns {Array<Bind>|undefined} bind strings
 */
function parseBindings (node, propertyName = 'bindings') {
  const bindings = parsePropertyFromSchema(node, propertyName, {
    type: 'phandle-array'
  })

  return bindings
    ? groupPhandleArrayBindings([].concat(...bindings))
    : undefined
}

/**
 * @param {Array<IntegerCell>} bindingCells
 * @returns {Array<Bind>}
 */
function groupPhandleArrayBindings (bindingCells) {
  return bindingCells.reduce((bindings, node) => {
    if (node.startsWith('&')) {
      bindings.push([node])
    } else {
      const last = bindings[bindings.length - 1]
      last.push(node)
    }

    return bindings
  }, []).map(binding => binding.join(' '))
}

const MACRO_CONTROL_BEHAVIOURS = [
  '&macro_tap',
  '&macro_press',
  '&macro_release',
  '&macro_tap_time',
  '&macro_wait_time',
  '&macro_pause_for_release'
]

/**
 * Group a flat list of bind strings at macro activation mode change
 * @param {Array<Bind>} bindings
 * @returns {Array<Array<Bind>>}
 */
function groupMacroBindings (bindings) {
  return bindings.reduce((rows, binding, i, arr) => {
    const behaviourBind = binding.split(' ')[0]

    if (MACRO_CONTROL_BEHAVIOURS.includes(behaviourBind)) {
      if (rows.at(-1).length > 0) {
        rows.push([])
      }

      rows.at(-1).push(binding)
      if (i < arr.length - 1) {
        rows.push([])
      }
    } else {
      rows.at(-1).push(binding)
    }

    return rows
  }, [[]])
}

/**
 * Parse a bind string into a tree of values and parameters
 * @param {Bind} binding
 * @returns {BindValue}
 */
function parseKeyBinding (binding, keymap) {
  const paramsPattern = /\((.+)\)/

  function parse (code) {
    const value = code.replace(paramsPattern, '')
    const params = get(code.match(paramsPattern), '[1]', '').split(',')
      .map(s => s.trim())
      .filter(s => s.length > 0)
      .map(parse)

    return {
      // TODO: some defines (e.g., in bat43) can involve multiple bind values
      // this replacement should happen before the bindings array is parsed
      value: get(keymap.defines, value, value),
      params
    }
  }

  const value = binding.match(/^(&.+?)\b/)[1]
  const params = filter(binding.replace(/^&.+?\b\s*/, '')
    .split(' '))
    .map(parse)

  return { value, params }
}

/**
 * Serialize a node of a BindValue tree
 * @param {BindValue} parsed
 * @returns {Bind}
 */
function encodeBindValue (parsed) {
  const params = (parsed.params || []).map(encodeBindValue)
  const paramString = params.length > 0 ? `(${params.join(',')})` : ''
  return parsed.value + paramString
}

/**
 * Serialize a top-level BindValue object
 * @param {BindValue} parsed
 * @returns {Bind}
 */
function encodeKeyBinding (parsed) {
  const { value, params } = parsed

  return `${value} ${params.map(encodeBindValue).join(' ')}`.trim()
}

module.exports = {
  parseBindings,
  parseKeyBinding,
  groupMacroBindings,
  groupPhandleArrayBindings,
  encodeKeyBinding
}
