const omitBy = require('lodash/omitBy')
const renderLayout = require('keymap-layout-tools/lib/render')

const { encodeKeyBinding } = require('./devicetree/bindings')

class KeymapValidationError extends Error {
  constructor (errors) {
    super()
    this.name = 'KeymapValidationError'
    this.errors = errors
  }
}

// TODO: Rename this to something like "hydrate"?
function encodeKeymap (layout, parsedKeymap) {
  /*
  This should basically become the standard format for a keymap when passed
  around the various layers of the backend.

  TODO: what do I want to do about keymap.json formatting? Should I call this a
  new version and update existing files on-the-fly?

  Also layer_names should be removed and the name (idenfitier?) + "label" should
  be given in the layer object.

  We can also analyze the unique set of behaviours used across all layers to
  determine ahead of time which headers to include.
  */
  return Object.assign({}, parsedKeymap, {
    layers: encodeLayers(parsedKeymap.layers, layout),
    combos: encodeCombos(parsedKeymap.combos)
  })
}

function encodeLayers (layers, layout) {
  return layers.map(layer => {
    // Newly created layers will specify name instead of identifier but still
    // use `layer.bindings` and `layer.label`
    if (layer.identifier || layer.bindings) {
      layer = {
        index: layer.index,
        name: layer.identifier || layer.name,
        labels: [],
        properties: {
          label: layer.label && { parsed: layer.label },
          bindings: layer.bindings && { parsed: layer.bindings },
          'sensor-bindings': layer.sensorBindings && { parsed: layer.sensorBindings }
        }
      }
    }

    if (layer.properties.bindings?.parsed) {
      const { parsed } = layer.properties.bindings
      layer.properties.bindings = {
        parsed,
        useRaw: true,
        raw: `<\n${renderLayout(layout, parsed.map(encodeKeyBinding))}\n>`
      }
    }

    if (layer.properties.label && !layer.properties.label.parsed) {
      delete layer.properties.label
    }

    layer.properties = omitBy(layer.properties, value => !value)

    return layer
  })
}

function encodeCombos (combos) {
  return combos.map(combo => {
    if (combo.identifier) {
      combo = {
        index: combo.index,
        name: combo.identifier,
        labels: [],
        properties: {
          bindings: combo.bindings && { parsed: combo.bindings },
          'key-positions': combo.keyPositions && { parsed: combo.keyPositions },
          'timeout-ms': combo.timeoutMs && { parsed: combo.timeoutMs },
          'slow-release': combo.slowRelease && { parsed: combo.slowRelease },
          layers: combo.layers?.length && { parsed: combo.layers }
        }
      }
    }

    combo.properties = omitBy(combo.properties, value => !value)

    if (!combo.properties.layers?.parsed?.length) {
      delete combo.properties.layers
    }

    return combo
  })
}

function parseKeymap (keymap) {
  // TODO: move all of this into extractKeymapData
  return Object.assign({}, keymap, {
    layers: keymap.layers.map((layer, i) => ({ ...layer, index: i })),
    combos: keymap.combos.map((combo, i) => ({ ...combo, index: i })),
    macros: keymap.macros.map((macro, i) => ({ ...macro, index: i })),
    behaviors: keymap.behaviors.map((behavior, i) => ({ ...behavior, index: i }))
  })
}

module.exports = {
  KeymapValidationError,
  encodeKeymap,
  encodeLayers,
  encodeCombos,
  parseKeymap
}
