const assert = require('assert')
const { ReferenceError } = assert
const difference = require('lodash/difference')
const filter = require('lodash/filter')
const isEqual = require('lodash/isEqual')
const map = require('lodash/map')
const partition = require('lodash/partition')
const reverse = require('lodash/reverse')

const { getNodeLabels, getLabeledItem } = require('./helpers/nodes')
const { getPropertyValues } = require('./helpers/properties')
const { sanitizeIdentifier } = require('./helpers/values')

class NodeCollection {
  managedProperties = []
  _cached_container_node = null
  _cached_container_tree_version = 0
  _cached_collection_nodes = null
  _cached_collection_tree_version = 0

  constructor (document, options = {}) {
    this.document = document
    if (options.managedProperties) { this.managedProperties = options.managedProperties }
    if (options.lookupContainerNode) { this.lookupContainerNode = options.lookupContainerNode.bind(this) }
    if (options.injectContainerNode) { this.injectContainerNode = options.injectContainerNode.bind(this) }
    if (options.lookupNodes) { this.lookupNodes = options.lookupNodes.bind(this) }
    if (options.injectNode) { this.injectNode = options.injectNode.bind(this) }
  }

  lookupContainerNode () { throw new ReferenceError('Provide `lookupContainerNode` in constructor or override this method') }
  injectContainerNode () { throw new ReferenceError('Provide `injectContainerNode` in constructor or override this method') }

  lookupNodes () { throw new ReferenceError('Provide `lookupNodes` in constructor or override this method') }
  injectNode () { throw new ReferenceError('Provide `injectNode` in constructor or override this method') }

  get container () {
    if (this._cached_container_tree_version !== this.document._parse_version) {
      delete this._cached_container_node
    }
    if (!this._cached_container_node) {
      this._cached_container_node = this.lookupContainerNode(this.document)
      this._cached_container_tree_version = this.document._parse_version
    }

    return this._cached_container_node
  }

  get nodes () {
    if (this._cached_collection_tree_version !== this.document._parse_version) {
      delete this._cached_collection_nodes
    }
    if (!this._cached_collection_nodes) {
      this._cached_collection_nodes = this.lookupNodes(this.document)
      this._cached_collection_tree_version = this.document._parse_version
    }

    return this._cached_collection_nodes
  }

  updateCollection (collection) {
    if (collection.length && !this.container) {
      this.injectContainerNode(this)
      this.document.flushChanges()
    }

    const originalIndices = [...this.nodes.keys()]
    const reusedIndices = filter(map(collection, 'index'), index => this.nodes[index])
    const removedIndices = difference(originalIndices, reusedIndices)
    const [updates, additions] = partition(collection, node => this.nodes[node.index])

    for (const node of updates) {
      this.updateNode(node.index, node)
    }

    for (const index of reverse(removedIndices)) {
      this.document.removeNode(this.nodes[index])
    }

    for (const node of additions) {
      this.injectNode(this, node)
      this.document.flushChanges()
      this.updateNode(this.nodes.length - 1, node)
    }
  }

  /**
   * Update an existing devicetree node
   * @param {Integer} index the position of the existing node to update
   * @param {Object} update the new node state to apply to devicetree
   * @param {String} update.name the devicetree node name
   * @param {Array<String>} update.labels zero or more devictree labels to apply to the node
   * @param {Object} update.properties mapping of property name to (serialized) property values
   */
  updateNode (index, update) {
    const existing = this.extractNode(this.nodes[index])
    this.updateNodeName(index, update, existing)
    this.updateNodeLabels(index, update, existing)
    this.updateNodeProperties(index, update, existing)
    this.cleanupNodeProperties(index, update, existing)
  }

  updateNodeName (index, update, existing) {
    if (existing.name !== update.name) {
      const identifier = this.nodes[index].childForFieldName('name')
      this.document.replaceNode(identifier, sanitizeIdentifier(update.name), {
        removeStartingWhitespace: false
      })
    }
  }

  updateNodeLabels (index, update, existing) {
    if (!isEqual(existing.labels, update.labels)) {
      this.document.replaceNodeLabels(this.nodes[index], update.labels)
    }
  }

  updateNodeProperties (index, update, existing) {
    const { properties } = update

    for (const name in properties) {
      const values = properties[name]

      if (!isEqual(values, existing.properties[name])) {
        this.document.setProperty(
          this.nodes[index],
          name,
          values
        )
      }
    }
  }

  cleanupNodeProperties (index, update, existing) {
    const { properties } = update
    for (const name of this.managedProperties) {
      if (name in existing.properties && !(name in properties)) {
        this.document.deleteProperty(this.nodes[index], name)
      }
    }
  }

  /**
   * Extract basic information from a devicetree node for convenience.
   * The output of this function is an object with structure:
   *  {
   *    name: String,
   *    labels: Array<String>
   *    properties: Object<name:values>
   *  }
   * This can be overridden to include additional details.
   * @param {SyntaxNode} node a devicetree node from which to extract info
   * @returns {Object}
   */
  extractNode (node) {
    // TODO: should there be a `Node` class?
    // TODO: Should there be a `PropertyCollection` with similar abstractions?
    assert(node.type === 'node')

    const name = node.childForFieldName('name').text
    const labels = getNodeLabels(node)
    const properties = node.namedChildren.reduce((acc, child) => {
      if (child.type === 'labeled_item') {
        child = getLabeledItem(child)
      }
      if (child.type === 'property') {
        const name = child.childForFieldName('name').text
        const values = getPropertyValues(node, name)
        const parsed = map(values, 'text')
        const raw = parsed.join(', ')
        acc[name] = { parsed, raw }
      }

      return acc
    }, {})

    return {
      name,
      labels,
      properties
    }
  }

  /**
   * Evaluate each node with `extractNode` and include an `index` property
   * @returns {Array<Object>}
   */
  extractCollection () {
    return this.nodes.map((node, index) => ({
      index, ...this.extractNode(node)
    }))
  }
}

module.exports = {
  NodeCollection
}
