const pick = require('lodash/pick')

const {
  findChildByIdentifier,
  findChildrenByIdentifier,
  findNodesWithCompatible
} = require('devicetree/lib/helpers')
const { NodeCollection } = require('devicetree/lib/node-collection')

const BehaviorCollection = require('./behaviors')
const { groupMacroBindings, encodeKeyBinding } = require('../bindings')

class MacroCollection extends BehaviorCollection {
  managedProperties = [
    'label',
    'tap-ms',
    'wait-ms'
  ]

  lookupContainerNode () {
    const rootNodes = findChildrenByIdentifier(this.document.tree.rootNode, '/')
    return findChildByIdentifier(rootNodes, 'macros')
  }

  injectContainerNode () {
    const keymap = findNodesWithCompatible(this.document.tree.rootNode, 'zmk,keymap').at(-1)
    this.document.insertTextBeforeNode(keymap, '\nmacros {};\n')
  }

  lookupNodes () {
    return findNodesWithCompatible(
      this.document.tree.rootNode,
      'zmk,behavior-macro'
    )
  }

  injectNode (_, macro) {
    const name = macro.name.replace(/[^a-zA-Z0-9_]/g, '_')
    const last = this.nodes.at(-1)
    const nodeContent = (
      `${name}: ${name} {
        compatible = "zmk,behavior-macro";
      };`
    )

    if (last) {
      this.document.insertTextAfterNode(last, nodeContent)
    } else {
      this.document.addChildNode(this.container, nodeContent)
    }
  }

  updateNode (index, update) {
    const serialized = serializeMacroBindings(update.properties.bindings?.parsed || [])
    update.properties.bindings = {
      useRaw: true,
      raw: serialized
    }

    super.updateNode(index, update)
  }

  legacyParse (node) {
    return pick(this.extractNode(node), ['name', 'labels', 'properties'])
  }

  legacyParseNodes () {
    return this.nodes.map(node => this.legacyParse(node))
  }

  cleanupNodeProperties (index, update, existing) {
    const { cleanupNodeProperties } = NodeCollection.prototype
    return cleanupNodeProperties.call(this, index, update, existing)
  }
}

function serializeMacroBindings (bindings) {
  const serialized = groupMacroBindings(
    bindings.map(encodeKeyBinding)
  ).map(row => row.join(' '))

  return serialized.length === 1
    ? [`<${serialized[0]}>`]
    : serialized.map(row => `<${row}>`)
}

module.exports = MacroCollection
