const compact = require('lodash/compact')
const { getParser } = require('devicetree')
const { resolveBehaviorParams } = require('zmk-data/parameters')

const KeymapDocument = require('./devicetree/keymap-document')
const {
  keymapUsesNodefreeConfigHelpers,
  keymapUsesMiryoku,
  keymapBindingsUsePreprocessorMacros,
  keymapHasNoRootNodes,
  keymapHasLayerBindingsWithNonBehaviorStarter,
  warningFlags: { USING_NON_ZMK_MACRO }
} = require('./devicetree/sanity-checks')
const { parseStringLiteral } = require('devicetree/lib/helpers/values')

async function extractKeymapData (keymapText) {
  const parser = await getParser()
  const document = new KeymapDocument(parser, keymapText)

  try {
    // Behaviors need post-processing to determine types for their binding cells
    const behaviors = document.behaviorCollection.legacyParseNodes()
    resolveBehaviorParams(behaviors)

    return {
      warningFlags: extractWarnings(document),
      defines: extractDefines(document),
      headers: extractLocalHeaders(document),
      chosen: document.getChosenProperties(),
      behaviors,
      layers: document.layerCollection.legacyParseNodes(),
      combos: document.comboCollection.legacyParseNodes(),
      macros: document.macroCollection.legacyParseNodes()
    }
  } catch (err) {
    if (keymapHasLayerBindingsWithNonBehaviorStarter(document)) {
      return {
        warningFlags: [USING_NON_ZMK_MACRO],
        defines: {},
        headers: [],
        chosen: {},
        behaviors: [],
        layers: [],
        combos: [],
        macros: []
      }
    }
    console.error(err)
    return {
      warningFlags: ['KEYMAP_PARSING_FAILED'],
      defines: {},
      headers: [],
      chosen: {},
      behaviors: [],
      layers: [],
      combos: [],
      macros: []
    }
  } finally {
    document.cleanup()
  }
}

function extractWarnings (document) {
  return compact([
    keymapHasNoRootNodes(document),
    keymapUsesNodefreeConfigHelpers(document),
    keymapUsesMiryoku(document),
    keymapBindingsUsePreprocessorMacros(document)
  ])
}

function extractDefines (document) {
  return document.tree.rootNode.namedChildren.reduce((acc, node) => {
    if (node.type === 'preproc_def') {
      const name = node.childForFieldName('name').text
      const value = node.childForFieldName('value')?.text.trim()

      acc[name] = value
    }

    return acc
  }, {})
}

function extractLocalHeaders (document) {
  return document.tree.rootNode.namedChildren.reduce((acc, node) => {
    if (node.type === 'preproc_include') {
      const path = node.childForFieldName('path')
      if (path.type === 'string_literal') {
        acc.push(parseStringLiteral(path))
      }
    }

    return acc
  }, [])
}

module.exports = extractKeymapData
