
/**
 *
 * @param {Tree|Node} tree Starting point for breadth-first traversal
 * @param {Object} [options={}]
 * @param {Integer} [options.maxDepth=-1]
 * @returns {Function} get next node, or undefined when traversal is complete
 */
function BreadthFirstIterator (tree, options = {}) {
  const { maxDepth = -1 } = options
  const queue = [{ depth: 0, node: tree.rootNode || tree }]

  return function next () {
    const current = queue.shift()
    if (current && (maxDepth === -1 || current.depth < maxDepth)) {
      queue.push(...current.node.namedChildren.map(node => ({
        node, depth: current.depth + 1
      })))
    }
    return current?.node
  }
}

/**
 *
 * @param {Tree|Node} tree Starting point for depth-first traversal
 * @param {Object} [options={}]
 * @param {Integer} [options.maxDepth=-1]
 * @returns {Function} get next node, or undefined when traversal is complete
 */
function DepthFirstIterator (tree, options = {}) {
  const { maxDepth = -1 } = options
  const stack = [{ depth: 0, node: tree.rootNode || tree }]

  return function next () {
    const current = stack.shift()
    if (current && (maxDepth === -1 || current.depth < maxDepth)) {
      stack.unshift(...current.node.namedChildren.map(node => ({
        node, depth: current.depth + 1
      })))
    }
    return current?.node
  }
}

/**
 * Find nodes in `tree` that satisfy the given `predicate` function.
 *
 * @param {Tree|SyntaxNode} tree
 * @param {Function} predicate
 * @param {Object} [options={}]
 * @param {Boolean} [options.single=false]
 * @param {Integer} [options.maxDepth=-1]
 * @param {Boolean} [options.depthFirst=false]
 * @returns {Array<SyntaxNode>|SyntaxNode} use options.single to return only the first matching node
 */
function searchTree (tree, predicate, options = {}) {
  const {
    single = false,
    maxDepth = -1,
    depthFirst = false
  } = options

  const iter = depthFirst
    ? DepthFirstIterator(tree, { maxDepth })
    : BreadthFirstIterator(tree, { maxDepth })

  const matches = []
  let node

  while ((node = iter())) {
    if (predicate(node)) {
      if (single) {
        return node
      }

      matches.push(node)
    }
  }

  if (single) {
    return undefined
  }

  return matches
}

function findNodeByIdentifier (tree, name) {
  return searchTree(tree, node => (
    node.type === 'node' &&
    node.childForFieldName('name').text === name
  ), { single: true })
}

function findLabeledItem (tree, name) {
  let node = searchTree(tree, node => (
    node.type === 'labeled_item' &&
    node.children[0].type === 'identifier' &&
    node.children[0].text === name
  ), { single: true })

  if (!node) {
    return null
  }

  do {
    node = node.childForFieldName('item')
  } while (node.type === 'labeled_item')

  return node
}

function createPredicateForCompatible (compatible) {
  let match

  if (compatible instanceof RegExp) {
    match = text => compatible.test(text)
  } else if (typeof compatible === 'string') {
    match = text => text === `"${compatible}"`
  } else if (typeof compatible === 'function') {
    match = compatible
  } else {
    throw new TypeError('Unexpected type ' + typeof compatible)
  }

  return node => (
    node.type === 'property' &&
    node.childForFieldName('name').text === 'compatible' &&
    match(node.childForFieldName('value').text)
  )
}

/**
 * Find single node including a `compatible` property with the given value.
 *
 * @param {Tree|SyntaxNode} tree
 * @param {String|RegExp|Function} compatible
 * @returns {SyntaxNode|undefined}
 */
function findNodeWithCompatible (tree, compatible) {
  // TODO: look into tree sitter queries again, doing these searches within web
  // assembly is probably much faster. Alternatively, performing this lookup as
  // a breadth-first search would likely save a lot of time since we're often
  // expecting to match nodes just inside the devicetree root node. It may also
  // be worth refining the depth-first search to avoid going deeper in some
  // situations -- if I'm matching a property string it doesn't make sense to
  // recurse into the property values.
  const predicate = createPredicateForCompatible(compatible)
  const propertyNode = searchTree(tree, predicate, { single: true })

  return propertyNode?.parent
}
/**
 * Find descendent nodes including a `compatible` property with the given value.
 *
 * @param {Tree|SyntaxNode} tree
 * @param {String|RegExp|Function} compatible
 * @returns {Array<SyntaxNode>}
 */
function findNodesWithCompatible (tree, compatible) {
  const predicate = createPredicateForCompatible(compatible)
  return searchTree(tree, predicate).map(property => property.parent)
}

module.exports = {
  BreadthFirstIterator,
  DepthFirstIterator,
  searchTree,
  findLabeledItem,
  findNodeByIdentifier,
  findNodeWithCompatible,
  findNodesWithCompatible
}
