const flatten = require('lodash/flatten')
const get = require('lodash/get')
const keyBy = require('lodash/keyBy')
const map = require('lodash/map')
const uniq = require('lodash/uniq')

const { getParser } = require('devicetree')
const { formatNode, getModelineConfiguration } = require('devicetree/lib/formatters')
const { behaviorBindings } = require('zmk-data/behaviors')

const KeymapDocument = require('./devicetree/keymap-document')
const { encodeKeymap } = require('./keymap')

const behavioursByBind = keyBy(behaviorBindings, 'code')

async function applyKeymapChanges (layout, keymap, original, featureFlags = {}) {
  // TODO: generate this higher up in the call stack, I don't think we need the
  // original keymap object anymore except for the `layer_names` property
  const encoded = encodeKeymap(layout, keymap)
  const behaviourHeaders = flatten(getBehavioursUsed(keymap).map(
    bind => get(behavioursByBind, [bind, 'includes'], [])
  ))

  const devicetreeKeymap = new KeymapDocument(await getParser(), original)
  devicetreeKeymap._enableChangeBuffering = true
  devicetreeKeymap.mergeUpdated(encoded, featureFlags)
  devicetreeKeymap.replaceBehaviourIncludes(behaviourHeaders)
  devicetreeKeymap.flushChanges()

  try {
    const config = getModelineConfiguration(devicetreeKeymap.tree)
    return formatNode(devicetreeKeymap.tree.rootNode, config).join('\n')
  } catch (err) {
    console.error('Failed to format devictree', err)
    return devicetreeKeymap.text
  } finally {
    devicetreeKeymap.cleanup()
  }
}

function getBehavioursUsed (keymap) {
  const keybinds = flatten(map(keymap.layers, layer => layer.properties?.bindings?.parsed || layer.bindings))
  const comboBinds = flatten(map(keymap.combos, 'bindings'))
  const macroBinds = flatten(map(keymap.macros, 'properties.bindings.parsed'))

  return uniq(map([
    ...keybinds,
    ...comboBinds,
    ...macroBinds
  ], 'value'))
}

module.exports = applyKeymapChanges
