const assert = require('assert')
const { sortBy } = require('lodash')

const TreeSitter = require('web-tree-sitter')

const { getPosition } = require('string-utils')
const { getNodeLabelNodes } = require('./helpers/nodes')
const { getProperty } = require('./helpers/properties')

let _parser

async function getParser () {
  if (!_parser) {
    await TreeSitter.init()
    const language = await TreeSitter.Language.load(

      // This is meant to make loading possible in a browser.
      global.window
        ? require('../data/tree-sitter-devicetree.wasm')
        : __dirname + '/../data/tree-sitter-devicetree.wasm'
    )
    _parser = new TreeSitter()
    _parser.setLanguage(language)
  }

  return _parser
}

class DevicetreeDocument {
  constructor (parser, text) {
    this.parser = parser
    this._parse_version = 0
    this._enableChangeBuffering = false
    this._bufferedChanges = []
    this.update(text)
  }

  cleanup () {
    this.tree.delete()
  }

  update (text, inputEdit = null) {
    if (inputEdit && this.tree && this._enableChangeBuffering) {
      const replacementText = text.slice(inputEdit.startIndex, inputEdit.newEndIndex)
      this._bufferedChanges.push({
        startIndex: inputEdit.startIndex,
        update: inputEdit,
        replacementText
      })
    } else {
      this.text = text
      this._reparse()
    }
  }

  _reparse () {
    this.tree && this.tree.delete()
    this.tree = this.parser.parse(this.text)
    this._parse_version++
  }

  flushChanges () {
    if (this._bufferedChanges.length === 0) {
      return
    }

    const updates = sortBy(this._bufferedChanges, 'startIndex')

    // console.log('flushing updates', updates)
    // console.log({ original: this.text })

    const chunks = updates.reduce((chunks, { update, replacementText }, i) => {
      const nextUpdate = updates[i + 1]
      const prevEnd = i > 0
        ? updates[i - 1].update.oldEndIndex
        : 0

      const preChunk = this.text.slice(prevEnd, update.startIndex)
      const chunk = replacementText
      chunks.push(preChunk)
      chunks.push(chunk)

      // console.log({
      //   preChunk,
      //   chunk
      // })

      if (!nextUpdate) {
        // console.log('selecting final cunk', {
        //   original: this.text,
        //   update,
        //   postChunk: this.text.slice(update.oldEndIndex)
        // })
        chunks.push(this.text.slice(update.oldEndIndex))
      }

      return chunks
    }, [])

    // console.log('generated chunks', chunks)

    this.text = chunks.join('')
    // console.log('generated text', this.text)
    this._reparse()
    this._bufferedChanges = []
  }

  removeNode (node) {
    this.replaceNode(node, '')
  }

  /**
   * Replace the contents of a syntax node with other (serialized) content.
   *
   * @param {SyntaxNode} node the node to be replaced
   * @param {String} newContent the devicetree source replacing the original node
   * @param {Object} options
   * @param {Bool} options.removeStartingWhitespace whether to attempt to start replacing at the start of `node`'s line
   */
  replaceNode (node, newContent, options = {}) {
    const { removeStartingWhitespace = true } = options
    const { text: documentText } = this

    // TODO: this all should probably go
    const precedingNewline = documentText.lastIndexOf('\n', node.startIndex)
    const hasStartingWhitespace = precedingNewline !== -1 && documentText.slice(precedingNewline + 1, node.startIndex).match(/\s+/)
    const start = hasStartingWhitespace && removeStartingWhitespace
      ? precedingNewline
      : node.startIndex

    const replacement = [
      documentText.slice(0, start),
      newContent,
      documentText.slice(node.endIndex)
    ].join('')

    const inputEdit = {
      startIndex: start,
      startPosition: getPosition(newContent, start),
      oldEndIndex: node.endIndex,
      oldEndPosition: node.endPosition,
      newEndIndex: start + newContent.length,
      newEndPosition: getPosition(replacement, start + newContent.length)
    }

    this.update(replacement, inputEdit)
  }

  replaceRange (startIndex, skipToIndex, newContent) {
    const { text: source } = this
    const pre = source.slice(0, startIndex)
    const post = source.slice(skipToIndex)
    const replacement = [pre, newContent, post].join('')
    const newEndIndex = startIndex + newContent.length
    const inputEdit = {
      startIndex,
      startPosition: getPosition(source, startIndex),
      oldEndIndex: skipToIndex,
      oldEndPosition: getPosition(source, skipToIndex),
      newEndIndex,
      newEndPosition: getPosition(replacement, newEndIndex)
    }

    return this.update(replacement, inputEdit)
  }

  /**
   * Replace any existing labels attached to the specified node.
   * @param {SyntaxNode} labeledNode
   * @param {Array<String>} newLabels
   */
  replaceNodeLabels (labeledNode, newLabels) {
    const labelNodes = getNodeLabelNodes(labeledNode)
    const sorted = sortBy(labelNodes, 'startIndex')
    const serializedLabels = newLabels.map(label => `${label}:`).join(' ')
    const start = (sorted[0] && sorted[0].parent) || labeledNode

    this.replaceRange(start.startIndex, labeledNode.startIndex, serializedLabels)
  }

  /**
   * Insert text content before a specified node
   * @param {SyntaxNode} node the node before which to insert
   * @param {String} newContent the new devicetree source to be inserted
   */
  insertTextBeforeNode (node, newContent) {
    this.replaceRange(node.startIndex, node.startIndex, newContent)
  }

  /**
   * Insert text content after a specified node
   * @param {SyntaxNode} node the node after which to insert
   * @param {String} newContent the new devicetree source to be inserted
   */
  insertTextAfterNode (node, newContent) {
    this.replaceRange(node.endIndex, node.endIndex, newContent)
  }

  /**
   * Append devicetree source to the specified node
   * @param {SyntaxNode} parentNode parent node in which to add content
   * @param {*} newContent the new devicetree source to be inserted
   */
  addChildNode (parentNode, newContent) {
    assert(parentNode.type === 'node')

    const closingBrace = parentNode.children.find(node => node.type === '}')
    return this.replaceRange(
      closingBrace.startIndex,
      closingBrace.startIndex,
      newContent
    )
  }

  /**
   * Set/create a property with the specified value in the specified node
   * @param {SyntaxNode} node target node in which to set property
   * @param {String} property name of property to set
   * @param {String|Boolean} value serialized devicetree value.
   */
  setProperty (node, property, value) {
    const existing = getProperty(node, property)

    if (typeof value === 'boolean' && !value) {
      if (existing) {
        this.deleteProperty(node, property)
      }

      return
    }

    const newText = value.length
      ? `${property} = ${value};`
      : `${property};`

    if (existing) {
      return this.replaceNode(
        existing.syntaxNode,
        newText,
        { removeStartingWhitespace: false }
      )
    }

    return this.addChildNode(node, `\n         ${newText}`)
  }

  /**
   * Remove a property from a specified devicetree node.
   * @param {SyntaxNode} node where to search for the named property
   * @param {String} property name of property to remove
   */
  deleteProperty (node, property) {
    const existing = getProperty(node, property)
    if (existing) {
      this.replaceNode(existing.syntaxNode, '')
    }
  }
}

module.exports = {
  getParser,
  DevicetreeDocument
}
