const assert = require('assert')
const reverse = require('lodash/reverse')

/**
 * Check if an ordered list of nodes are consecutive
 * Nodes are consecutive if each node is adjacent to the next, so that no other
 * sibling nodes exist between them.
 * @param {Array<SyntaxNode>} nodes
 * @returns {Boolean}
 */
function nodesAreConsecutive (nodes) {
  return nodes.every((node, i) => (
    i < nodes.length - 1
      ? node.nextSibling.id === nodes[i + 1].id
      : true
  ))
}

/**
 * Get the string value of each reference label attached to the given node.
 * Labels are in in descending order of depth.
 * @param {SyntaxNode} node
 * @returns {Array<String>}
 */
function getNodeLabels (node) {
  return getNodeLabelNodes(node).map(label => label.text)
}

// blech
function getNodeLabelNodes (node) {
  return getNodeChildrenByFieldName(node.parent, 'label')
}

/**
 * Descend into a chain of labeled_item nodes to get at the actual item
 * @param {SyntaxNode} labeledItemNode syntax node for a devicetree label
 * @returns {SyntaxNode}
 */
function getLabeledItem (labeledItemNode) {
  assert(labeledItemNode.type === 'labeled_item')
  return labeledItemNode.childForFieldName('item')
}

function getNodeChildrenByFieldName (node, fieldName) {
  const children = []

  // Iteration is done with a cursor here because only TreeCursor will directly
  // tell us what field is associated with the current node. The node itself
  // will not tell us about its own field, and Node.childForFieldName(field)
  // will only tell us about a single child with the specified field.
  const cursor = node.walk()
  cursor.gotoFirstChild()

  do {
    const isNamed = cursor.currentNode().isNamed()
    const isField = cursor.currentFieldName() === fieldName
    if (isNamed && isField) {
      children.push(cursor.currentNode())
    }
  } while (cursor.gotoNextSibling())

  cursor.delete()

  return children
}

/**
 * Find a single child of the given node with a matching identifier
 * @param {SyntaxNode} node
 * @param {Function|String} nameOrMatch
 * @returns {SyntaxNode}
 */
function findChildrenByIdentifier (node, nameOrMatch) {
  const match = typeof nameOrMatch === 'string'
    ? text => text === nameOrMatch
    : nameOrMatch

  return node.namedChildren.filter(node => (
    node.type === 'node' &&
    node.children.find(sub => (
      sub.type === 'identifier' &&
      match(sub.text)
    ))
  ))
}

/**
 * Find a child of the given node(s) with a matching name.
 * If multiple nodes are given to search through, they will be searched in
 * reverse order so that the "latest" instance of a node takes precedence.
 * @param {Array<SyntaxNode>|SyntaxNode} nodes
 * @param {RegExp|String} nameOrMatch
 */
function findChildByIdentifier (nodes, nameOrMatch) {
  nodes = [].concat(nodes)
  for (const node of reverse(nodes)) {
    const match = findChildrenByIdentifier(node, nameOrMatch)[0]
    if (match) {
      return match
    }
  }
}

function listNodes (nodes, opts = {}) {
  const { stripNewlines = true, limit = 50 } = opts
  for (const node of nodes) {
    let value = node.text
    if (stripNewlines) value = value.replace(/\n/g, '')
    if (limit) value = value.slice(0, limit)

    console.log(node.id, `[${node.type}]`, '->', value)
  }
}

module.exports = {
  nodesAreConsecutive,
  getNodeLabels,
  getNodeLabelNodes,
  getLabeledItem,
  getNodeChildrenByFieldName,
  findChildByIdentifier,
  findChildrenByIdentifier,
  listNodes
}
