const difference = require('lodash/difference')
const filter = require('lodash/filter')
const isEqual = require('lodash/isEqual')
const map = require('lodash/map')

const {
  findChildrenByIdentifier,
  findNodesWithCompatible,
  getLabeledItem,
  getNodeLabels,
  parsePropertiesFromSchema,
  serializePropertyFromSchema
} = require('devicetree/lib/helpers')
const { NodeCollection } = require('devicetree/lib/node-collection')

const { groupPhandleArrayBindings, parseKeyBinding, encodeKeyBinding } = require('../bindings')

function isSorted (array) {
  return array.every((value, i, arr) => (
    i === arr.length - 1 ||
    arr[i] < arr[i + 1]
  ))
}

// TODO: extract the `zmk,keymap` schema and handle the `childProperties` prop
const layerSchema = {
  label: {
    type: 'string'
  },
  bindings: {
    type: 'phandle-array'
  },
  'sensor-bindings': {
    type: 'phandle-array'
  }
}

class LayerCollection extends NodeCollection {
  managedProperties = [
    'label',
    'bindings',
    'sensor-bindings'
  ]

  lookupContainerNode () {
    return findNodesWithCompatible(this.document.tree.rootNode, 'zmk,keymap').at(-1)
  }

  injectContainerNode () {
    const rootNodes = findChildrenByIdentifier(this.document.tree.rootNode, '/')
    this.document.addChildNode(
      rootNodes.at(-1),
      '\nkeymap { compatible = "zmk,keymap"; };\n'
    )
  }

  lookupNodes () {
    if (!this.container) {
      return []
    }

    return this.container.namedChildren.reduce((nodes, syntaxNode) => {
      if (syntaxNode.type === 'labeled_item') {
        syntaxNode = getLabeledItem(syntaxNode)
      }
      if (syntaxNode.type === 'node') {
        nodes.push(syntaxNode)
      }

      return nodes
    }, [])
  }

  injectNode (_, layer) {
    const last = this.nodes.at(-1)
    const name = (layer.name || 'new_layer').replace(/[^a-zA-Z0-9_]/g, '_')
    const nodeContent = `${name} {};`

    if (last) {
      this.document.insertTextAfterNode(last, nodeContent)
    } else {
      this.document.addChildNode(this.container, nodeContent)
    }
  }

  updateCollection (collection) {
    const originalIndices = [...this.nodes.keys()]

    super.updateCollection(collection)
    this.updateLayerPositions(collection, originalIndices)
  }

  /**
   * Sort layer nodes according to the updates array ordering.
   * This could perhaps be implemented generically in NodeCollection or even in
   * DevicetreeDocument but that's complicated, especially considering scenarios
   * like BehaviorCollection and MacroCollection which may be dealing with nodes
   * that are subsets of a single nodes children, or even nodes that aren't even
   * children of the same parent.
   *
   * For now it only makes sense for layer nodes to be sortable, so here you go.
   *
   * @param {Array<Object>} updates same argument passed to updateCollection
   * @param {Array<Integer>} originalIndices node indices from before updates were applied
   */
  updateLayerPositions (updates, originalIndices) {
    const reusedIndices = filter(map(updates, 'index'), index => this.nodes[index])
    const removedIndices = difference(originalIndices, reusedIndices)
    const positionMap = [...originalIndices]

    // Add new nodes to the end of the position map. Their current position does
    // not matter because they're already sorted relative to eachother and the
    // mapping will let us reorder everything at the end.
    for (const index in updates) {
      const update = updates[index]
      const nextIndex = positionMap.length
      if (update.index === undefined || !originalIndices.includes(update.index)) {
        update.index = nextIndex
        positionMap[nextIndex] = nextIndex
      }
    }

    // Shift every mapped position for nodes following removed nodes.
    for (let i = removedIndices.length - 1; i >= 0; --i) {
      const index = removedIndices[i]
      positionMap[index] = null
      for (let j = index + 1; j < positionMap.length; j++) {
        if (positionMap[j] !== null) {
          positionMap[j] -= 1
        }
      }
    }

    const targetIndexOrder = map(updates, 'index')
    if (targetIndexOrder.length > 0 && !isSorted(targetIndexOrder)) {
      this.document.flushChanges()
      this.document.replaceRange(
        this.nodes[0].startIndex,
        this.nodes.at(-1).endIndex,
        targetIndexOrder
          .map(index => positionMap[index])
          .map(index => this.nodes[index].text)
          .join('\n')
      )
    }
  }

  updateNodeProperties (index, update, existing) {
    const { properties } = update
    for (const name in properties) {
      const prop = properties[name]
      const propSchema = layerSchema[name]

      if (!isEqual(prop, existing.properties[name])) {
        const serialized = propSchema && !prop.useRaw
          ? (
              // Not ideal to have this type-check here, but our handling of this
              // type goes beyond devicetree.
              propSchema.type !== 'phandle-array'
                ? serializePropertyFromSchema(prop, propSchema)
                : prop.parsed
                  .map(binding => encodeKeyBinding(binding))
                  .map(bindString => `<${bindString}>`)
                  .join(', ')
            )
          : prop.raw

        this.document.setProperty(this.nodes[index], name, serialized)
      }
    }
  }

  extractNode (node) {
    const name = node.childForFieldName('name').text
    const labels = getNodeLabels(node)
    const properties = parsePropertiesFromSchema(node, layerSchema)

    if (properties.bindings) {
      properties.bindings.parsed = (
        groupPhandleArrayBindings(properties.bindings.parsed)
          .map(parseKeyBinding)
      )
    }

    if (properties['sensor-bindings']) {
      properties['sensor-bindings'].parsed = (
        groupPhandleArrayBindings(properties['sensor-bindings'].parsed)
          .map(parseKeyBinding)
      )
    }

    return { name, labels, properties }
  }

  legacyParse (node) {
    const { name, labels, properties } = this.extractNode(node)
    const legacyNode = {
      name,
      labels,
      properties,
      identifier: name
    }

    if (properties.label) {
      legacyNode.label = properties.label.parsed
    }
    if (properties.bindings) {
      legacyNode.bindings = properties.bindings.parsed
    }
    if (properties['sensor-bindings']) {
      legacyNode.sensorBindings = properties['sensor-bindings'].parsed
    }

    return legacyNode
  }

  legacyParseNodes () {
    return this.nodes.map(node => this.legacyParse(node))
  }
}

module.exports = LayerCollection
