const { searchTree } = require('./trees')

const isNegativeInteger = node => (
  node.type === 'unary_expression' &&
  node.childForFieldName('operator').type === '-' &&
  node.childForFieldName('argument').type === 'integer_literal'
)

const isNumberishType = node => (
  ['identifier', 'integer_literal'].includes(node.type) || isNegativeInteger(node)
)

const isBindingType = node => (
  // call_expression node includes modifier functions like `LC(A)`
  ['identifier', 'integer_literal', 'reference', 'call_expression'].includes(node.type)
)

const isBindingRef = node => node.type === 'reference'

const extractInteger = node => (
  node.type === 'integer_literal' || isNegativeInteger(node)
    ? Number(node.text)
    : node.text
)

function parseStringLiteral (value) {
  return value?.text?.slice(1, -1)
}

function parseIntegerCells (values) {
  return parseIntegerArray(values)
}

function parseIntegerArray (values) {
  return values?.flatMap(cells => (
    cells.namedChildren
      .filter(isNumberishType)
      .map(extractInteger)
  ))
}

function parsePhandle (values) {
  return parsePhandles(values)[0]
}

function parsePhandles (values) {
  return values?.flatMap(cells => (
    cells.namedChildren
      .filter(isBindingRef)
      .map(value => value.text)
  ))
}

function parsePhandleArray (values) {
  return values?.flatMap(cells => (
    cells.namedChildren
      .filter(isBindingType)
      .map(value => value.text)
  ))
}

function parseIntegerUnion (values) {
  return values?.flatMap(value => (
    searchTree(value, isNumberishType, { depthFirst: true })
  )).map(extractInteger)
}

function sanitizeIdentifier (name) {
  return name
    .replace(/[^a-zA-Z0-9,._+-]/g, '_')
    .slice(0, 31)
}

function serializeIntegerCell (value) {
  value = value.toString()
  if (value.match(/-\d+/)) {
    value = `(${value})`
  }

  return value
}

function serializeIntValue (value) {
  return `<${serializeIntegerCell(value)}>`
}

function serializeArrayValue (values) {
  return `<${values.map(serializeIntegerCell).join(' ')}>`
}

module.exports = {
  parseStringLiteral,
  parseIntegerCells,
  parseIntegerArray,
  parsePhandle,
  parsePhandleArray,
  parsePhandles,
  parseIntegerUnion,
  sanitizeIdentifier,
  serializeIntValue,
  serializeArrayValue
}
