const isEqual = require('lodash/isEqual')

const {
  findChildByIdentifier,
  findChildrenByIdentifier,
  findNodesWithCompatible,
  findNodeProperty,
  getPropertyValues,
  parsePropertyFromSchema,
  sanitizeIdentifier,
  serializePropertyFromSchema
} = require('devicetree/lib/helpers')
const { NodeCollection } = require('devicetree/lib/node-collection')
const { behaviorSchemas } = require('zmk-data/behaviors')

const { parseKeyBinding, encodeKeyBinding } = require('../bindings')

function resolveBehaviorBindings (behavior) {
  const bindingsValues = getPropertyValues(behavior, 'bindings') || []
  const bindings = bindingsValues.flatMap(bindingsValue => (
    bindingsValue.namedChildren.flatMap(bindCode => bindCode.text)
  ))

  return { bindings }
}

class BehaviorCollection extends NodeCollection {
  lookupContainerNode () {
    const rootNodes = findChildrenByIdentifier(this.document.tree.rootNode, '/')
    return findChildByIdentifier(rootNodes, 'behaviors')
  }

  injectContainerNode () {
    const keymap = findNodesWithCompatible(this.document.tree.rootNode, 'zmk,keymap').at(-1)
    this.document.insertTextBeforeNode(keymap, '\nbehaviors {};\n')
  }

  lookupNodes () {
    return findNodesWithCompatible(this.document.tree.rootNode, value => (
      value.startsWith('"zmk,behavior-') &&
      value !== '"zmk,behavior-macro"'
    ))
  }

  injectNode (_, behavior) {
    const name = sanitizeIdentifier(behavior.name)
    const last = this.nodes.at(-1)
    const nodeContent = `${behavior.labels[0]}: ${name} {
      compatible = ${behavior.properties.compatible.raw};
    };`

    // new node will be updated with full set of properties later, but for now
    // a known behavior has to be set to make sure this node is not filtered out
    if (last) {
      this.document.insertTextAfterNode(last, nodeContent)
    } else {
      this.document.addChildNode(this.container, nodeContent)
    }
  }

  extractNode (node) {
    const compatible = findNodeProperty(node, 'compatible')?.namedChildren[1].text.slice(1, -1)

    const schema = behaviorSchemas[compatible]
    const extracted = super.extractNode(node)
    const { name: identifier, labels } = extracted

    extracted.properties.compatible.parsed = compatible

    if (schema) {
      for (const prop in schema.properties) {
        const propSchema = schema.properties[prop]
        const extractedProp = extracted.properties[prop]
        if (extractedProp) {
          extractedProp.parsed = parsePropertyFromSchema(node, prop, propSchema)

          if (propSchema.type === 'phandle-array') {
            const bindStrings = extractedProp.parsed
              .reduce((bindings, node) => {
                if (node.startsWith('&')) {
                  bindings.push([node])
                } else {
                  const last = bindings[bindings.length - 1]
                  last.push(node)
                }

                return bindings
              }, [])
              .map(group => group.join(' '))

            extractedProp.parsed = bindStrings.map(bind => parseKeyBinding(bind, {}))
          }
        }
      }
    }

    const bindingCells = extracted.properties['#binding-cells']?.parsed || 0
    const label = extracted.properties.label?.parsed

    return Object.assign(
      extracted,
      { binding: `&${labels[0]}` },
      { code: `&${labels[0]}` },
      { aliases: labels.map(label => `&${label}`) },
      { identifier },
      { bindingCells },
      label && { label },
      compatible && { compatible },

      resolveBehaviorBindings(node)
    )
  }

  legacyParseNodes () {
    return this.nodes.map(node => this.extractNode(node))
  }

  updateNodeProperties (index, update, existing) {
    const { properties } = update
    const compatible = findNodeProperty(this.nodes[index], 'compatible')?.namedChildren[1].text.slice(1, -1)
    const schema = behaviorSchemas[compatible]

    for (const name in properties) {
      const prop = properties[name]
      const propSchema = schema?.properties?.[name]

      if (!isEqual(prop, existing.properties[name])) {
        const serialized = propSchema && !prop.useRaw
          ? (
              // Not ideal to have this type-check here, but our handling of this
              // type goes beyond devicetree.
              propSchema.type !== 'phandle-array'
                ? serializePropertyFromSchema(prop, propSchema)
                : prop.parsed
                  .map(binding => encodeKeyBinding(binding))
                  .map(bindString => `<${bindString}>`)
                  .join(', ')
            )
          : prop.raw

        this.document.setProperty(this.nodes[index], name, serialized)
      }
    }
  }

  cleanupNodeProperties (index, update, existing) {
    for (const prop in existing.properties) {
      if (!(prop in update.properties)) {
        this.document.deleteProperty(this.nodes[index], prop)
      }
    }
  }
}

module.exports = BehaviorCollection
