const isEqual = require('lodash/isEqual')

const {
  findNodeWithCompatible,
  findNodesWithCompatible,
  getLabeledItem,
  getNodeLabels,
  parsePropertiesFromSchema,
  serializePropertyFromSchema
} = require('devicetree/lib/helpers')
const { NodeCollection } = require('devicetree/lib/node-collection')

const { groupPhandleArrayBindings, parseKeyBinding, encodeKeyBinding } = require('../bindings')

// TODO: extract the `zmk,combos` schema and handle the `childProperties` prop
const comboSchema = {
  bindings: { type: 'phandle-array' },
  'key-positions': { type: 'array' },
  'timeout-ms': { type: 'int' },
  'slow-release': { type: 'boolean' },
  layers: { type: 'array' }
}

class ComboCollection extends NodeCollection {
  managedProperties = [
    'bindings',
    'key-positions',
    'timeout-ms',
    'layers',
    'slow-release'
  ]

  lookupContainerNode () {
    return findNodeWithCompatible(this.document.tree.rootNode, 'zmk,combos')
  }

  injectContainerNode () {
    // TODO: this sucks and is making many other things worse.
    // I've encountered keymap files that specify multiple root devicetree nodes
    // which ZMK accepts and merges together, and multiple keymap nodes which
    // devicetree accepts and selects the last one.
    // Working around this is a pain in the ass and is hurting performance so
    // the best thing to do is stop supporting this and present a warning to the
    // user explaining why their keymap isn't being parsed as expected.
    const keymap = findNodesWithCompatible(this.document.tree.rootNode, 'zmk,keymap').at(-1)
    this.document.insertTextBeforeNode(keymap, `
      combos {
        compatible = "zmk,combos";
      };
    `)
  }

  lookupNodes () {
    if (!this.container) {
      return []
    }

    return this.container.namedChildren.reduce((nodes, syntaxNode) => {
      if (syntaxNode.type === 'labeled_item') {
        syntaxNode = getLabeledItem(syntaxNode)
      }
      if (syntaxNode.type === 'node') {
        nodes.push(syntaxNode)
      }

      return nodes
    }, [])
  }

  injectNode (_, combo) {
    const name = combo.name.replace(/[^a-zA-Z0-9_]/g, '_')
    const last = this.nodes.at(-1)
    const nodeContent = `${name} {};`

    if (last) {
      this.document.insertTextAfterNode(last, nodeContent)
    } else {
      this.document.addChildNode(this.container, nodeContent)
    }
  }

  updateNodeProperties (index, update, existing) {
    const { properties } = update
    for (const name in properties) {
      const prop = properties[name]
      const propSchema = comboSchema[name]

      if (!isEqual(prop, existing.properties[name])) {
        const serialized = propSchema && !prop.useRaw
          ? (
              // Not ideal to have this type-check here, but our handling of this
              // type goes beyond devicetree.
              propSchema.type !== 'phandle-array'
                ? serializePropertyFromSchema(prop, propSchema)
                : prop.parsed
                  .map(binding => encodeKeyBinding(binding))
                  .map(bindString => `<${bindString}>`)
                  .join(', ')
            )
          : prop.raw

        this.document.setProperty(this.nodes[index], name, serialized)
      }
    }

    if (!update.properties.layers) {
      this.document.deleteProperty(this.nodes[index], 'layers')
    }
  }

  extractNode (node) {
    const name = node.childForFieldName('name').text
    const labels = getNodeLabels(node)
    const properties = parsePropertiesFromSchema(node, comboSchema)

    if (properties.bindings) {
      properties.bindings.parsed = (
        groupPhandleArrayBindings(properties.bindings.parsed)
          .map(parseKeyBinding)
      )
    }

    return { name, labels, properties }
  }

  legacyParse (node) {
    const { name, labels, properties } = this.extractNode(node)
    const legacyNode = {
      name,
      labels,
      properties,
      identifier: name
    }

    const legacyMapping = {
      bindings: 'bindings',
      'timeout-ms': 'timeoutMs',
      'key-positions': 'keyPositions',
      'slow-release': 'slowRelease',
      layers: 'layers'
    }

    for (const prop of Object.keys(legacyMapping)) {
      if (properties[prop]) {
        legacyNode[legacyMapping[prop]] = properties[prop].parsed
      }
    }

    if (legacyNode.layers?.length === 0 || isEqual(legacyNode.layers, [-1])) {
      delete legacyNode.layers
    }

    return legacyNode
  }

  legacyParseNodes () {
    return this.nodes.map(node => this.legacyParse(node))
  }
}

module.exports = ComboCollection
