const { getNodeChildrenByFieldName } = require('./nodes')
const {
  parseStringLiteral,
  parseIntegerCells,
  parseIntegerArray,
  parsePhandles,
  parsePhandle,
  parsePhandleArray,
  parseIntegerUnion,
  serializeIntValue,
  serializeArrayValue
} = require('./values')

/** @typedef {{ parsed: any, raw: string, useRaw?: boolean }} PropertyValue */
/** @typedef {Object.<string, PropertyValue>} Properties */

/**
 * Get a property with the given name in this node, if it exists.
 * @param {SyntaxNode} node
 * @param {String} propertyName
 * @returns {SyntaxNode|undefined}
 */
function findNodeProperty (node, propertyName) {
  return node.children.find(node => (
    node.type === 'property' &&
    node.children[0].text === propertyName
  ))
}

function getPropertyNode (node, propertyName) {
  return node.namedChildren.find(child => (
    child.type === 'property' &&
    child.childForFieldName('name')?.text === propertyName
  ))
}

function getProperty (node, property) {
  const propertyNode = node.children.find(node => (
    node.type === 'property' &&
    node.children[0].text === property
  ))

  return propertyNode && {
    syntaxNode: propertyNode,
    name: property,
    value: propertyNode.children[2]?.text
  }
}

function getPropertyValue (node, property) {
  property = typeof property === 'string'
    ? getPropertyNode(node, property)
    : property

  return property?.childForFieldName('value')
}

function getPropertyValues (node, property) {
  const propertyNode = getPropertyNode(node, property)
  return propertyNode && getNodeChildrenByFieldName(propertyNode, 'value')
}

/**
 * @param {SyntaxNode} node devicetree node from which to extract property values
 * @param {Object} schema devicetree schema of a spcecific node
 * @return {Properties} properties
 */
function parsePropertiesFromSchema (node, schema) {
  return Object.keys(schema).reduce((properties, prop) => {
    const propSchema = schema[prop]
    const values = parsePropertyFromSchema(node, prop, propSchema)

    if (values !== undefined) {
      properties[prop] = {
        parsed: values
      }
    }

    return properties
  }, {})
}

/**
 * @param {SyntaxNode} node devicetree node from which to extract a property value
 * @param {String} property name of property
 * @param {Object} schema devicetree schema of specific property
 */
function parsePropertyFromSchema (node, property, schema) {
  const propertyNode = findNodeProperty(node, property)
  const values = getPropertyValues(node, property)

  switch (schema.type) {
    case 'string':
      return parseStringLiteral(values?.[0])

    case 'boolean':
      return !!propertyNode

    case 'int':
      return parseIntegerCells(values)?.[0]

    case 'array':
      return parseIntegerArray(values)

    case 'phandle':
      return parsePhandle(values)

    case 'phandles':
      return parsePhandles(values)

    case 'phandle-array':
      return parsePhandleArray(values)

    case 'integer-union':
      return parseIntegerUnion(values)
  }
}

function serializePropertyFromSchema (property, schema) {
  // TODO: Probably make separate functions?
  switch (schema.type) {
    case 'string':
      return `"${property.parsed}"`

    case 'boolean':
      // This is a weird case because instead of an explicit value we use the
      // presense of the property (name) to indicate true or false. Note that
      // this will get more complicated when supporting the `/delete-property/`
      // flag (?)
      return property.parsed

    case 'int':
      return serializeIntValue(property.parsed)

    case 'array':
      return serializeArrayValue(property.parsed)

    case 'phandle':
      return property.parsed.map(phandle => `<${phandle}>`).join(', ')

    case 'phandles':
    case 'phandle-array':
      // TODO: Grouping binding cells into separate phandle-arrays shouldn't
      // be the default behavior but right now the only place this would be
      // unacceptably weird is the bindings property for layer and macro nodes
      // which already do their own formatting explicitly.
      return property.parsed
        .reduce((bindings, node) => {
          if (node.startsWith('&')) {
            bindings.push([node])
          } else {
            const last = bindings[bindings.length - 1]
            last.push(node)
          }

          return bindings
        }, [])
        .map(group => `<${group.join(' ')}>`)
        .join(', ')

    case 'integer-union':
      return `<(${property.parsed.map(v => v.toString()).join('|')})>`
  }
}

module.exports = {
  findNodeProperty,
  getProperty,
  getPropertyNode,
  getPropertyValue,
  getPropertyValues,
  parsePropertiesFromSchema,
  parsePropertyFromSchema,
  serializePropertyFromSchema
}
