const times = require('lodash/times')
const { behaviorSchemas, behaviorBindings } = require('./behaviors')

/**
 * Parsing keymap gives us a number of custom behaviour definitions
 * Some custom behaviours may reference other behaviours
 * Some custom behaviours may reference other custom behaviours
 *
 * We need to add all new behaviour to a new map upon loading
 * After populating the map with all new behaviours we can resolve bind params
 *
 * Keep a list of "visited" behaviours
 * For each behaviour
 * For each behaviour referenced in bindings
 * Resolve behaviour reference to get params list
 * If behaviour reference already exists in list of visited behaviours, bail
 * If number of references to resolve exceeds X, bail
 */

function BehaviorResolutionError (message) {
  this.message = message
  this.name = 'BehaviorResolutionError'
}

/** @typedef {string} Identifier */
/** @typedef {string} Label */
/** @typedef {{ parsed: any, raw: string, useRaw?: boolean }} PropertyValue */
/** @typedef {Object.<string, PropertyValue>} Properties */
/** @typedef {{ name: string, labels: Array<string>, properties: Properties }} ParsedBehavior  */

function parseNumBindingCells (behavior, prop = '#binding-cells') {
  const parsed = behavior.properties?.[prop]?.parsed

  if (typeof parsed === 'number') {
    return parsed
  }

  // For unrecognized behaviors we don't have the schema to parse the
  // "#binding-cells" property like we'd expect, so let's assume its a single
  // integer literal and parse the raw value
  const raw = behavior.properties?.[prop]?.raw || '<>'
  const match = raw.match(/<(\d+)>/)

  if (match) {
    return Number(match[1])
  }
}

function behaviorMap (map, behavior) {
  for (const label of behavior.labels) {
    map[`&${label}`] = behavior
  }
  return map
}

/**
 * Attempt to provide parameter types for behaviours with binding-cells.
 * @param {Array<ParsedBehavior>} behaviors - custom behavior definitions
 */
function resolveBehaviorParams (behaviors) {
  const standardBindingsMap = behaviorBindings.reduce(behaviorMap, {})
  // Here we attempt to resolve the binding to a "known" behaviour for which
  // I have already manually described the parameters. Failing that, this
  // would fall back to a single "code" parameter for each. This assumes
  // that a custom hold-tap wouldn't want something like "hold for layer"
  // and "tap to connect to BT profile 1".
  const newBindingsMap = behaviors.reduce(behaviorMap, {})

  /**
   * @param {ParsedBehavior} behavior
   * @param {Array<string>} [queue=[]]
   * @returns {void}
   */
  function resolve (behavior, queue = []) {
    if ('params' in behavior) {
      return behavior.params
    }

    const compatible = behavior.properties?.compatible?.parsed
    const schema = behaviorSchemas[compatible]

    const bindingsType = schema?.properties?.bindings?.type
    const bindingCells = parseNumBindingCells(behavior) ?? parseNumBindingCells(behavior, '#sensor-binding-cells')
    const bindings = (behavior.properties?.bindings?.parsed || [])

    // console.log('resolving for', {
    //   compatible,
    //   schema,
    //   bindingsType,
    //   bindingCells,
    //   bindings
    // })

    if (!bindingCells) {
      return []
    }

    // A `bindings` prop of type `phandle-array` means the bindings are already
    // given along with binding cell values and don't need further params.
    if (bindingsType === 'phandle-array') {
      return []
    }

    if (!bindings.length) {
      return times(bindingCells, () => ({ type: 'string', raw: true }))
    }

    // This implementation relies on the existing pattern for behaviors which
    // reference other behaviors without parameters (hold-tap and sticky-key)
    // will only provide a single parameter to each referenced behavior without
    // concerning itself with the referenced behavior's binding cell count.
    // A possible future exception to this is parameterized macros:
    //  https://github.com/zmkfirmware/zmk/pull/1232#issuecomment-1478977559
    // This will require re-visiting this solution.
    return bindings.reduce((params, binding) => {
      // This implies the bindings property we're inspecting is actually a
      // phandles-array type (behavior with params) and not phandle/phandles.
      // This shouldn't happen and stricter checking earlier on would be wise.
      if (!binding.startsWith('&')) {
        return params
      }

      if (queue.includes(binding)) {
        throw new BehaviorResolutionError(`Circular reference in bindings: "&${behavior.labels[0]}" -> "${binding}".`)
      } else if (queue.length > 3) {
        throw new BehaviorResolutionError('Reference depth limit exceeded.')
      }

      const nextBehavior = newBindingsMap[binding] || standardBindingsMap[binding]

      // Not sure if I'm ok with this. Previously the idea was that a behavior
      // referencing an unrecognized behavior cannot resolve parameters at all,
      // whereas now it just treats that as having a single raw parameter and
      // includes an error message property that nothing else uses.
      if (!nextBehavior) {
        behavior.error = `Unrecognized behavior binding "${binding}".`
        return [...params, { raw: true }]
      }

      try {
        nextBehavior.params = resolve(nextBehavior, [...queue, binding])
        params.push(nextBehavior.params[0] || { type: 'placeholder' })
      } catch (error) {
        if (error instanceof BehaviorResolutionError) {
          nextBehavior.error = error.message
          nextBehavior.params = []
        }
        throw error
      }

      return params
    }, [])
  }

  for (const behavior of behaviors) {
    // if (!behavior.properties.bindings) {
    //   continue
    // }

    try {
      behavior.params = resolve(behavior)
    } catch (error) {
      if (!(error instanceof BehaviorResolutionError)) {
        throw error
      }

      console.error('Failed to resolve behavior parameters', error)
      behavior.error = error.message
      behavior.params = times(parseNumBindingCells(behavior) || 0, () => ({ raw: true }))
    }
  }
}

module.exports = {
  resolveBehaviorParams
}
